import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
export default {
	components: {
		uniSearchBar
	},
	data() {
		return {
			space: ' ',
			tryTo: '您可以试试搜索',
			tryList: ['微积分', '电路理论', '微机原理', '毛概', '数理方程与特殊函数']
		}
	},
	onLoad() {
		uni.setNavigationBarTitle({
			title: '首页'
		})
	},
	methods: {
		inputFocus() {
			
		},
		searchConfirm(e) {
			uni.navigateTo({
				url: 'search?text=' + encodeURIComponent(JSON.stringify(e.detail.value))
			})
		}
	}
}
